html{
	background-image: url("img/mainbg.jpeg");
	background-repeat: no-repeat;
	background-size: cover;
}
body {
	
  margin: 0;
  font-family: 'HelveticaNeue', 'Arial Narrow' ;
  
}

.navbar {
	background-color: white;
	position: relative;
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	justify-content: space-between;
	padding: 0rem 1rem;
	line-height: 1;
	min-height: 10vh;
	box-shadow: 10px 10px 8px #888888;
	z-index: 2;
}
#big ul{
  padding: 0;
}
#big ul li {
	display: inline;
	padding: 2px 6px;
	border-radius: 10px;

	margin-right: 30px;
}

.navbar ul li .navlinks {
	color: black;
	text-decoration: none;
}

*,
::after,
::before {
  box-sizing: border-box;
  background: transparent;
}

.close {
	display: none !important;
}

.btn-menu {
	display: none;
	cursor: pointer;
	transition: all 0.5s ease-in;
	padding-bottom: 0.2rem;
}
.btn-menu.show {
	display: block;
}
.btn-menu .btn-line {
	width: 28px;
	height: 3px;
	background: black;
	margin-top: 5px;
	transition: all 0.5s ease-in;
}
.btn-menu.closeb {
	transform: rotate(180deg);
}
.btn-menu.closeb .btn-line:nth-child(1) {
	transform: rotate(45deg) translate(5px, 5px);
	background: black ;
}
.btn-menu.closeb .btn-line:nth-child(2) {
	visibility: hidden;
}
.btn-menu.closeb .btn-line:nth-child(3) {
	transform: rotate(-45deg) translate(7px, -6px);
	background: black;
}

#small {
  max-height: 0;
  overflow: hidden;
  opacity: 0;
  min-width: 100%;
	transition: all 0.5s ease-in-out;
	
	
}
#small.show {
	opacity: 1;
	max-height: 100vh;
}

#small ul li .navlinks {
	text-decoration: none;
	color: black;
}
#small ul {
	list-style-type: none;
	padding-left: 0;
	margin-top: 0;
}
#small ul li {
	padding-bottom: 0.1rem;
}
.main {
	display: flex;
	height: 90vh;
  position: relative;
  justify-content: space-around;
  max-width: 100vw;
  align-items: center;
}
.info{
display: flex;
flex-direction: column;
justify-content: center;

}
.main h2{
  font-size: 50px;
  font-weight: 100;
  
}
.art img {
  background: none;
  width: 100%;
  height: 100%;
  object-fit: contain;

 
}
.art{
  display: flex;
  align-items: center;
  max-width: 50vw;
  
}
.main h1{
  
  font-size: 80px;
  font-weight: 100;
  line-height: 1;
  margin-bottom: 0;
}

.main h2:after {
  content: '.';
  animation: dots 1s steps(5, end) infinite;}

@keyframes dots {
  0%, 20% {
    color: rgba(0,0,0,0);
    text-shadow:
      .25em 0 0 rgba(0,0,0,0),
      .5em 0 0 rgba(0,0,0,0);}
  40% {
    color: black;
    text-shadow:
      .25em 0 0 rgba(0,0,0,0),
      .5em 0 0 rgba(0,0,0,0);}
  60% {
    text-shadow:
      .25em 0 0 black,
      .5em 0 0 rgba(0,0,0,0);}
  80%, 100% {
    text-shadow:
      .25em 0 0 black,
      .5em 0 0 black;}}



#abouthead{
	text-align: center;
}

.about h1{
	font-size: 60px;
	margin-bottom: 0;
}
.about h2{
	font-size: 40px;
}
.visioncontainer img{
	float: right;
	max-width: 200px;
}
.visioncontainer span{
	
	font-size: 45px;
}
.about{
	padding: 1rem;
}
.mission{
	display: grid;
	grid-template-columns: repeat(7,1fr);
	list-style: none;
	grid-gap: 7px;
	padding-left: 0;
}

.mitem{
	background-color: white;
	
	border: ridge black 2px;
	border-radius: 5px;
	padding: 0.5rem;
	text-align: center;
	font-size: large;
}
.mitem img{ 
	max-width: 100px;
}
#contact{
	padding: 1rem;
	
}
#contact h1{
	font-size: 60px;
	margin-bottom: 0;
	text-align: center;
}



.boxes {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-evenly;
	align-items: center;
	margin-top: 1rem; }
	.boxes div {
	  font-size: 2rem;
	  border: 3px black solid;
	  background-color: rgba(255, 255, 255, 0.578);
	  padding: 1.5rem 2.5rem;
	  margin-bottom: 3rem;
	  transition: all 0.5s ease-out; }
	  .boxes div:hover {
		padding: 0.5rem 1.5rem;
		background: black;
		color: white; }
		.boxes div:hover span {
		  color: white; }


.brand  h2{
	display: flex;
	align-items: center;
	}
	.brand h2 img{
		max-height: 5vh;
	}


#services{
	
	padding: 2rem;
}

#services h1{
	text-align: center;
	font-size: 50px;
}
.services{
	display: grid;
	grid-template-columns: repeat(3,1fr);
	grid-gap: 3px;
}
.services div{
	background-color: rgba(255, 255, 255, 1);
	border-radius: 10px;
	font-size: 25px;
	border: black ridge 2px;	

}
.services div h2{
	text-align: center;
	font-size: 40px;
}
.services div ul{
	list-style: none;
}
.services div li{
	padding: .3rem;
}



@media screen and (max-width: 950px){
	.main h1{
	  font-size: 60px;
	}
	.main h2{
	  font-size: 40px;
	}
	.art {
	  max-width: 40vw;
	}
	.mission{
		grid-template-columns: repeat(3,1fr);
		
	}
	.services{
		grid-template-columns: 1fr;
		grid-gap: 6px;
	}
	
  } 
  @media screen and (max-width: 670px){
	  .visioncontainer img{
		  float: none;
		  margin: 0 auto;
		  display: block;
		  
	  }
	.main .info h1{
	  font-weight: 400;
	}
	.main .info h2{
	  font-weight: 300;
	}
	.art{
	  display: none;
	}
	.mission{
		grid-template-columns: 1fr;
	}
	
	
  }

  
  